import { Navigate } from 'react-router-dom';
import React from 'react';

// 
const Home = React.lazy(() => import('@/views/home/index'));

// 
const routes = [
	{
		path: '/',
		element: <Navigate to={'/home'}></Navigate>
	},
	{
		path: '/home',
		element: <Home></Home>
	}
];

export default routes;
