import { configureStore } from '@reduxjs/toolkit';
import { persistReducer, persistStore } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import globalReducer from '@/store/modules/globalReducer';
import userReducer from '@/store/modules/userReducer';

// reducer
const globalReducerPersisCfg = {
	key: 'global',
	storage,
	whitelist: ['currentSectionId', 'scrollY'] // whitelist
};

const store = configureStore({
	reducer: {
		global: persistReducer(globalReducerPersisCfg, globalReducer), // persistReducerreducer
		user: userReducer
	},
	middleware: (getDefaultMiddleware) =>
		getDefaultMiddleware({
			serializableCheck: {
				ignoredActions: ['persist/PERSIST', 'persist/REHYDRATE'] // Redux Toolkitredux-persist
			}
		})
});

export const persistor = persistStore(store); // store
export default store;
